.share {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
}

.shareWrapper {
  padding: 10px;
}

.shareTop {
  display: flex;
  align-items: center;
}

.shareProfileImg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}

.shareInput {
  border: none;
  width: 80%;
}

.shareInput:focus {
  outline: none;
}

.shareHr {
  margin: 20px;
}

.shareBottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.shareOptions{
    display: flex;
    margin-left: 20px;
}

.shareOption{
    display: flex;
    align-items: center;
    margin-right: 15px;
    cursor: pointer;
}

.shareIcon{
    font-size: 18px;
    margin-right: 3px;
}

.shareOptionText{
    font-size: 14px;
    font-weight: 500;
}

.shareButton{
    border: none;
    padding: 7px;
    border-radius: 5px;
    background-color: green;
    font-weight: 500;
    margin-right: 20px;
    cursor: pointer;
    color: white;
}

.sharePhotoLink{
  width: calc(100% - 103px);
  margin-left: 20px;
  border: none;
  padding: 10px;
  margin-bottom: 15px;
}

.shareButtonOk{
  margin-left: 10px;
  background-color: rgb(0, 92, 128);
}

.shareNewImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.newShareOptions{
  margin-top: 10px;
}

.shareLocation{
  width: 100px;
  border: none;
  padding: 10px;
  margin-bottom: 5px;
}

@media (max-width: 700px) {
  .share .shareOptionText{
    display: none;
  }
}